<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process207"></a>

<p><br></p>
<p><span class="rvts17">在绞刑绳上（</span><span class="rvts50">On the Ropes</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:721,Y:95</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：韦典（</span><span class="rvts20">Gwydian</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">营救韦典摆脱困境</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1263.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在黑坑洞的绞刑场上，韦典的双手已经被捆上绳索。白魔导师头领正在准备行刑。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1264.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">这将是相当困难的一场战斗。战斗过程中，第一波会出现黑油虚空（免疫土元素），第二波会出现火焰虚空（免疫火元素），并使整个绞刑场处于混乱状态。</span>
<br><span class="rvts21">注意尽可能的进入韦典所在的绞刑架顶层，避免被死灵火焰烧伤。另外尽可能的保护韦典，一旦他死亡就意味着该任务的失败，并且也会使</span><span class="rvts20"></span><span class="rvts21">机会之窗</span><span class="rvts20"></span><span class="rvts21">支线失败，从而失去向汉南格学习增强源力的机会。</span>
<br><span class="rvts21">使用治疗和降雨技能可以有效抵制地狱火的蔓延，另外祝福技能也可以使地狱火焰变为治疗。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1265.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">拯救了韦典，他会第一时间支援自己的家人。注意，在进入黑坑洞绞刑场之前，我们就会遇到韦典的家人被绑架并即将被杀掉的场景。</span>
<br><span class="rvts21">如果在救韦典之前，已经帮助了他的家人，那么他的家人将会是安然无恙的。他会对你表示感激，并赠与你额外的技能书。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1266.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">拯救韦典后，可以再次去回廊树林处找到韦典的师傅汉南格，并告诉她你已经帮助了韦典（详见</span><span class="rvts20"></span><span class="rvts21">机会之窗</span><span class="rvts20"></span><span class="rvts21">），她会教你增强源力技能。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



